<template>
  <g-body-layout
    :pageIndex="pageNumber"
    :pageSize="pageSize"
    :total="total"
    :filterText.sync="filterText"
    @pageIndexChange="pageIndexChange"
    @pageSizeChange="pageSizeChange"
    @suffixSearchCallback="onFilterReady"
  >
    <!-- 按钮组插槽 -->
    <template #btnGroup>
      <g-btn-group :buttonLists="btnList" @btnCallBack="btnGroupClick">
      </g-btn-group>
    </template>
    <!-- g-ndo-table插槽 -->
    <template #table>
      <g-ndo-table
        :id="id"
        :handName="'工序建模'"
        :dataList="dataList"
        :isTableLoading="loading"
        :total="total"
        @rowClick="rowClick"
      ></g-ndo-table>
    </template>
    <!-- 详细内容插槽 -->
    <template #content>
      <create-or-edit-operation
        :ndoEditParams="ndoEditParams"
        :refreshTag="refreshTag"
        :saveTag="saveTag"
        :resetTag="resetTag"
        @childRefresh="childRefresh($event)"
        @rowClick="rowClick($event)"
        v-if="isShowEdit"
      ></create-or-edit-operation>
      <g-empty v-else> </g-empty>
    </template>
  </g-body-layout>
</template>

<script>
import CreateOrEditOperation from "./create-or-edit-operation/create-or-edit-operation.vue";
import GCopyNdo from "@/components/g-copy-ndo/g-copy-ndo.vue";
import { GNdoPageBase } from "@/utils";

export default {
  name: "OperationModeling",
  mixins: [GNdoPageBase],
  components: {
    CreateOrEditOperation,
  },

  data() {
    return {
      isTableLoading: false,
      filterText: "",
    };
  },

  mounted() {},
  created() {
    this.getBtnList();
  },
  methods: {
    getBtnList() {
      // 获取按钮列表
      this.btnList = [
        {
          text: this.AppConst.button.create,
          icon: this.AppConst.icon.create,
          eventName: this.AppConst.button.create,
          display: true,
        },
        {
          text: this.AppConst.button.copy,
          icon: this.AppConst.icon.copy,
          eventName: this.AppConst.button.copy,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.delete,
          icon: this.AppConst.icon.delete,
          eventName: this.AppConst.button.delete,
          display: !!this.id,
        },
        {
          text: this.AppConst.button.refresh,
          icon: this.AppConst.icon.refresh,
          eventName: this.AppConst.button.refresh,
          right: true,
          display: this.isShowEdit,
        },
        {
          text: this.AppConst.button.save,
          icon: this.AppConst.icon.save,
          eventName: this.AppConst.button.save,
          right: true,
          type: "primary",
          display: this.isShowEdit,
        },
      ];
    },
    copy() {
      this.createModal(
        { title: "复制", width: "600px" },
        {
          component: GCopyNdo,
          props: {
            entity: {
              copyValue: this.dataList[0].name,
              value: this.dataList[0].name,
            },
          },
          on: {
            success: (res) => {
              console.log(res);
              // 手动延时
              this.loading = true;
              setTimeout(() => {
                this.loading = false;
                this.copySuccess(res.value, res.copyValue);
                this.refreshGoFirstPage();
              }, 500);
            },
          },
        }
      );
    },
    delete() {
      // 删除确认
      this.$confirm({
        title: "确认删除",
        content: "确认删除该工序吗？删除后无法恢复。",
        onOk: () => {
          this.loading = true;
          setTimeout(() => {
            this.loading = false;
            // 消息提示
            this.deleteSuccess();
            // 刷新
            this.refresh();
          }, 500);
        },
      });
    },
    fetchData(request) {
      this.loading = true;
      setTimeout(() => {
        request.finishedCallback();
        // 填充dataList 示例数据
        let dataList = [];
        const operationTypes = [
          "加工工序",
          "装配工序",
          "检验工序",
          "热处理工序",
          "表面处理工序",
          "包装工序"
        ];
        
        const departments = [
          "机加部",
          "装配部",
          "质检部",
          "热处理部",
          "表面处理部",
          "包装部"
        ];
        
        const statuses = ["启用", "禁用", "开发中"];
        
        for (let i = 0; i < 25; i++) {
          const typeIndex = i % 6;
          let operationCode, name, description, standardTime;
          
          // 根据工序类型生成不同的工序数据
          switch(typeIndex) {
            case 0: // 加工工序
              const machiningOps = [
                { code: "OP001", name: "车削", desc: "金属车削加工", time: 30 },
                { code: "OP002", name: "铣削", desc: "金属铣削加工", time: 45 },
                { code: "OP003", name: "钻孔", desc: "金属钻孔加工", time: 25 },
                { code: "OP004", name: "磨削", desc: "金属表面磨削", time: 40 }
              ];
              const machiningIndex = i % 4;
              operationCode = machiningOps[machiningIndex].code;
              name = machiningOps[machiningIndex].name;
              description = machiningOps[machiningIndex].desc;
              standardTime = machiningOps[machiningIndex].time;
              break;
            
            case 1: // 装配工序
              const assemblyOps = [
                { code: "AS001", name: "组装主体", desc: "组装产品主体结构", time: 60 },
                { code: "AS002", name: "连接零件", desc: "连接各个零部件", time: 45 },
                { code: "AS003", name: "固定紧固", desc: "紧固连接件", time: 30 }
              ];
              const assemblyIndex = i % 3;
              operationCode = assemblyOps[assemblyIndex].code;
              name = assemblyOps[assemblyIndex].name;
              description = assemblyOps[assemblyIndex].desc;
              standardTime = assemblyOps[assemblyIndex].time;
              break;
            
            case 2: // 检验工序
              const inspectionOps = [
                { code: "IN001", name: "尺寸检验", desc: "检测产品尺寸", time: 20 },
                { code: "IN002", name: "外观检验", desc: "检测产品外观", time: 15 },
                { code: "IN003", name: "功能测试", desc: "测试产品功能", time: 30 }
              ];
              const inspectionIndex = i % 3;
              operationCode = inspectionOps[inspectionIndex].code;
              name = inspectionOps[inspectionIndex].name;
              description = inspectionOps[inspectionIndex].desc;
              standardTime = inspectionOps[inspectionIndex].time;
              break;
            
            case 3: // 热处理工序
              const heatOps = [
                { code: "HT001", name: "淬火", desc: "金属淬火处理", time: 120 },
                { code: "HT002", name: "回火", desc: "金属回火处理", time: 180 },
                { code: "HT003", name: "退火", desc: "金属退火处理", time: 240 }
              ];
              const heatIndex = i % 3;
              operationCode = heatOps[heatIndex].code;
              name = heatOps[heatIndex].name;
              description = heatOps[heatIndex].desc;
              standardTime = heatOps[heatIndex].time;
              break;
            
            case 4: // 表面处理工序
              const surfaceOps = [
                { code: "SF001", name: "喷漆", desc: "表面喷漆处理", time: 50 },
                { code: "SF002", name: "电镀", desc: "表面电镀处理", time: 90 },
                { code: "SF003", name: "抛光", desc: "表面抛光处理", time: 40 }
              ];
              const surfaceIndex = i % 3;
              operationCode = surfaceOps[surfaceIndex].code;
              name = surfaceOps[surfaceIndex].name;
              description = surfaceOps[surfaceIndex].desc;
              standardTime = surfaceOps[surfaceIndex].time;
              break;
            
            case 5: // 包装工序
              const packagingOps = [
                { code: "PK001", name: "内包装", desc: "产品内包装", time: 15 },
                { code: "PK002", name: "外包装", desc: "产品外包装", time: 20 }
              ];
              const packagingIndex = i % 2;
              operationCode = packagingOps[packagingIndex].code;
              name = packagingOps[packagingIndex].name;
              description = packagingOps[packagingIndex].desc;
              standardTime = packagingOps[packagingIndex].time;
              break;
          }
          
          dataList[i] = {
            id: i + 1,
            operationCode: `${operationCode}${String(i + 1).padStart(2, "0")}`,
            name: name,
            operationType: operationTypes[typeIndex],
            department: departments[typeIndex],
            description: description,
            standardTime: standardTime,
            standardUnit: "分钟",
            status: statuses[i % 3],
            createdBy: "admin",
            createdTime: "2024-03-19 " + (10 + i % 10) + ":" + (i % 60),
          };
        }

        // filter 过滤
        if (this.filterText) {
          console.log(this.filterText);
          dataList = dataList.filter((item) => {
            return item.operationCode.indexOf(this.filterText) > -1 || 
                   item.name.indexOf(this.filterText) > -1 ||
                   item.operationType.indexOf(this.filterText) > -1 ||
                   item.department.indexOf(this.filterText) > -1 ||
                   item.description.indexOf(this.filterText) > -1;
          });
        }

        request.successCallback({
          items: dataList,
          totalCount: dataList.length * 2,
        });
      }, 1000);
    },
  },
};
</script>

<style lang="scss" scoped></style> 